import React, { useCallback, useEffect } from 'react';
import './App.css';
import { Background } from '@codecademy/gamut-styles';
import { Text, Card, ProgressBar, StrokeButton } from '@codecademy/gamut';
import { MiniChevronLeftIcon, MiniChevronRightIcon } from '@codecademy/gamut-icons';
import * as d3 from "d3";
import { codeText, state0, state1, state2 } from './constants';

const states = [{...state0}, {...state1}, {...state2}];

const getStackVariableClass = (section, frame, variableIndex) => {
  return `${section}_${frame}_${variableIndex}`;
}

const getGuid = () => {
  return `id${Math.floor(Math.random() * Math.pow(10, 10))}`;
}

const getArrowPointerId = (guid) => {
  return `arrowPointer${guid}`;
}

function App() {
  const [stateIndex, setStateIndex] = React.useState(0);
  const [guid, setGuid] = React.useState(getGuid());

  const renderArrowPointers = () => {
    const container = d3.select(`.${guid}`);
    if (container.selectAll('svg').empty()) {
      container.append('svg')
        .attr('class', 'arrowOverlay')
        .append("svg:defs").append("svg:marker")
        .attr("id", getArrowPointerId(guid))
        .attr("refX", 12)
        .attr("refY", 6)
        .attr("markerWidth", 20)
        .attr("markerHeight", 20)
        .attr("orient", "auto")
        .append("path")
        .attr("d", "M 0 0 12 6 0 12 3 6")
        .style("fill", "red");    
    }

    const svg = container.select('.arrowOverlay')
      .attr('left', '0px')
      .attr('top', '0px')
      .attr('width', '100%')
      .attr('height', '100%');

    const arrowData: [string, string][] = [];
    states[stateIndex].stack.forEach((frame, frameIndex) => {
      frame.variables?.forEach((variable, variableIndex) => {
        if (variable['pointingTo'] !== undefined) {
          arrowData.push([getStackVariableClass('stack', frameIndex, variableIndex), variable['pointingTo']]);
        }
      });
    });

    const getSourceContainer = (d) => {
      const className = d[0];
      return container.select(`.${className}`).node();
    }
    const getTargetContainer = (d) => {
      const blockIndex = d[1];
      const blocks = container.select('.jssh-heapCollection').selectAll('.jssh-block');
      return blocks?.nodes()[blockIndex];
    }

    // from an element, find its relative x or y position compared to its container
    const getRelativeX = (el) => {
      return el?.getBoundingClientRect().x - container?.node()?.getBoundingClientRect().x;
    }
    const getRelativeY = (el) => {
      return el?.getBoundingClientRect().y - container?.node()?.getBoundingClientRect().y;
    }

    const arrows = svg.selectAll('.jssh-pointerArrow')
      .data(arrowData);
    arrows.enter()
      .append('line')
      .attr('class', 'jssh-pointerArrow')
      .merge(arrows)
      .attr('x1', (d) => {
        const sourceContainer = getSourceContainer(d);
        const leftBoundary = getRelativeX(sourceContainer);
        return leftBoundary + (sourceContainer?.getBoundingClientRect().width / 2);
      })
      .attr('y1', (d) => {
        const sourceContainer = getSourceContainer(d);
        const topBoundary = getRelativeY(sourceContainer);
        return topBoundary + (sourceContainer?.getBoundingClientRect().height / 2);
      })
      .attr('x2', (d) => {
        const targetContainer = getTargetContainer(d);
        const leftBoundary = getRelativeX(targetContainer);
        return leftBoundary;
      })
      .attr('y2', (d) => {
        const targetContainer = getTargetContainer(d);
        const topBoundary = getRelativeY(targetContainer);
        return topBoundary + (targetContainer?.getBoundingClientRect().height / 2);
      })
      .attr("marker-end", `url(#${getArrowPointerId(guid)})`);
    arrows.exit().remove();
  }

  React.useEffect(() => {
    renderArrowPointers();
  }, [stateIndex]);

  React.useEffect(() => {
    window.addEventListener('resize', renderArrowPointers);
    return () => {
      window.removeEventListener('resize', renderArrowPointers);
    };
  }, [stateIndex]);

  const getPercentage = () => {
    return stateIndex / 2 * 100; 
  }

  const getProgressText = () => {
    return `${stateIndex + 1}/3`;
  }

  const incrementIndex = () => {
    const maxState = states.length - 1;
    if (stateIndex >= maxState) {
      setStateIndex(0);
    } else {
      setStateIndex(stateIndex + 1);
    }
  }

  const decrementIndex = () => {
    const maxState = states.length - 1;
    if (stateIndex < 1) {
      setStateIndex(maxState);
    } else {
      setStateIndex(stateIndex - 1);
    }
  }

  return (<div className='jssh-outerContainer'>
    <div className='jssh-sectionOuter'>
        <Background bg="beige" className='jssh-section'>
          <Text as="h3">Code</Text>
          <Card className='jssh-block jssh-code-block'>
            <pre>
              <code>
                {codeText.map((codeLine, i) => {
                  const isHighlighted = states[stateIndex]?.highlightedLine === i;
                    return <div key={i} className={`jssh-codeLine${i}`}>
                      <span className={`jssh-lineNumber${isHighlighted ? ' jssh-highlightedLine': ''}`}>
                        {i + 1}
                      </span>
                      <span>{codeLine}</span>
                    </div>;
                })}
              </code>
            </pre>
          </Card>
        </Background>
      </div>
    <div className={`jssh-container ${guid}`}>
      <div className='jssh-sectionOuter'>
        <Background bg="beige" className='jssh-section'>
          <Text as="h3">Stack</Text>
          <div className='jssh-blockContainer'>
            {states[stateIndex].stack.map((frame, i) => (
              <Block frame={frame} key={i} frameIndex={i} section='stack'/>
            ))}
          </div>
        </Background>
      </div>
      <div className='jssh-sectionOuter'>
        <Background bg="beige" className='jssh-section'>
          <Text as="h3">Heap</Text>
          <div className='jssh-blockContainer jssh-heapCollection'>
            {states[stateIndex].heap.map((frame, i) => (
              <Block frame={frame} key={i} frameIndex={i} section='heap'/>
            ))}
          </div>
        </Background>
      </div>
    </div>
    <div className='jssh-navigationContainer'>
      <div className='jssh-navigationButtons'>
        <StrokeButton mode="light" onClick={decrementIndex} size="normal" className='jssh-navButton'>
          <MiniChevronLeftIcon/> Back
        </StrokeButton>
        <StrokeButton mode="light" onClick={incrementIndex} size="normal" className='jssh-navButton'>
          Next <MiniChevronRightIcon/>
        </StrokeButton>
      </div>
      <Text as="p">{getProgressText()}</Text>
      <ProgressBar percent={getPercentage()} variant='light' minimumPercent={5}/>
    </div>
  </div>);
};

const Block = ({frame, frameIndex, section}) => {
  return (<Card className='jssh-block'>
    <Text as='h4'>{frame.title}</Text>
    {frame.textContent && <Text as='h5'>
      {frame.textContent}  
    </Text>}
    {frame.variables?.length && <table className='jssh-table'>
      <tbody>
        {frame.variables.map((variable, i) => (<tr key={i}>
          {variable.name && <td>{variable.name}</td>}
          <td className={getStackVariableClass(section, frameIndex, i)}>{variable.value && variable.value}</td>
        </tr>))}
      </tbody>
    </table>}
  </Card>);
}

export default App;
